import { Company } from '@app/model';

export class TinCheckViewModel {
  /** "company" or "household" */
  get companyLabel() {
    return this.isHousehold ? 'household' : 'company';
  }

  /** "Company" or "Household" */
  get CompanyLabel() {
    return this.isHousehold ? 'Household' : 'Company';
  }

  isAdmin = false;
  isHousehold = false;
  isEditingLegalName = false;
  fein: string;
  legalName: string;
  matchedLegalName: string;
  preEditFein: string;
  preEditLegalName: string;
  serviceResponse = 'No TinCheck service response yet';
}
