import { Component, OnInit, ViewChild } from '@angular/core';
import { ngIfAnim } from '@animations';
import { first } from 'rxjs/operators';

import { SaverFactoryService, WellKnownRoutes } from '@core';
import { TinCheckComponent } from '@app/tin-check';

import { CompanyFedTaxViewService } from '../company-fed-tax-view.service';
import { CompanyStateTaxViewService } from '../company-state-tax-view.service';
import { HouseholdFedTaxViewService } from '../household-fed-tax-view.service';
import { FederalTaxViewModel } from '../company-tax-info-vm';
import { HouseholdFilers, HouseholdFilersStatus, CompanyTax } from '@model';
import {
  ICompanyFederalTaxes,
  TaxBehaviorWrapper
} from '@app/core/services/tax-services/tax-models';
import {
  TaxSelectorService,
  TaxCatalogKey
} from '@app/core/services/tax-services/tax-selector.service';

@Component({
  selector: 'sp-co-fed-tax',
  templateUrl: './company-fed-tax-info.component.html',
  styleUrls: ['../company-tax-info-tab.scss'],
  animations: [ngIfAnim]
})
export class CompanyFedTaxInfoComponent implements OnInit {
  fedTaxVm: FederalTaxViewModel;
  hhFilers: HouseholdFilers;
  fedCompanyTaxes: ICompanyFederalTaxes;
  futaTax: TaxBehaviorWrapper<CompanyTax>;
  HouseholdFilersStatus = HouseholdFilersStatus;
  WellKnownRoutes = WellKnownRoutes;

  @ViewChild('tincheck') tincheck: TinCheckComponent;

  constructor(
    private companyFedTaxInfoViewService: CompanyFedTaxViewService,
    private householdFedTaxViewService: HouseholdFedTaxViewService,
    private saverFactory: SaverFactoryService,
    private stateTaxInfoViewService: CompanyStateTaxViewService,
    private taxSelectorService: TaxSelectorService
  ) {}

  ngOnInit() {
    this.companyFedTaxInfoViewService.fedTaxVm$.pipe(first()).subscribe(vm => (this.fedTaxVm = vm));
    this.householdFedTaxViewService.hhFilers$
      .pipe(first())
      .subscribe(snapshot => (this.hhFilers = <any>{ ...snapshot }));
    this.companyFedTaxInfoViewService.federalTaxes$.pipe(first()).subscribe(taxes => {
      this.fedCompanyTaxes = taxes;
      this.futaTax = this.taxSelectorService.select(TaxCatalogKey.Futa, this.fedCompanyTaxes.taxes);
    });

    // Clear current state (if any)
    this.stateTaxInfoViewService.setCurrentState();
  }

  canLeaveAfterSave() {
    return this.save();
  }

  private save() {
    const saver = this.saverFactory
      .create()
      .add(this.companyFedTaxInfoViewService.saveCompany(this.fedTaxVm));

    if (this.fedTaxVm.isHousehold) {
      saver.add(this.householdFedTaxViewService.save(this.hhFilers));
    }

    saver.add(this.companyFedTaxInfoViewService.saveTaxes(this.fedCompanyTaxes));

    return saver.go();
  }

  setFeinModified() {
    this.tincheck.unverifyFein();
  }

  validateFein() {
    // Avoid calling validation ------
    // if (this.fedTaxVm.fein &&
    //   this.fedTaxVm.feinStatus !== FeinStatus.Verified
    // ) {
    //   this.tincheck.validate();
    // }
  }
}
