import { Injectable } from '@angular/core';

import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, first, map, take } from 'rxjs/operators';

import { FeinStatus, REASON_SERVICE_UNAVAILABLE } from '@model';
import { CompanyService } from '@entity';
import { FederalTaxViewModel } from '@app/company-tax-info/company-tax-info-vm';
import { SessionService } from '@session';
import { TinCheckService, TinCheckResponse, isBadFeinNumber } from '@core';
import { TinCheckViewModel } from './tin-check-vm';

export const MAX_VALIDATION_COUNT = 3;

@Injectable({providedIn: 'root'})
export class TinCheckViewService {

  readOnly$: Observable<boolean>;

  constructor(
    private companyService: CompanyService,
    private sessionService: SessionService,
    private tinCheckService: TinCheckService
  ) {
    this.readOnly$ = sessionService.readOnly$; // pass-thru to component
  }

  /**
   * Run a TinCheck on this FEIN/LegalName combination
   * @returns Terminating observable of the TinCheck service response.
   * Must subscribe but need not unsubscribe.
   */
  check(fein: string, legalName: string): Observable<TinCheckResponse> {
    return this.tinCheckService.check(fein, legalName);
  }

  detectLegalNameChangeAfterVerify(fedTaxVm: FederalTaxViewModel) {
    this.companyService.detectLegalNameChangeAfterVerify(fedTaxVm);
  }

  /**
   * Get the TinCheckViewModel from the Federal Tax VM & the company.
   */
  getTinCheckViewModel(fedTaxVm: FederalTaxViewModel): Observable<TinCheckViewModel> {
    const { fein, feinStatus, legalName, legalNameVerified } = fedTaxVm;
    return combineLatest(
      this.companyService.currentCompany$,
      this.sessionService.isAdmin$
    ).pipe(
      first(),
      map(([company, isAdmin]) => {
        const vm = new TinCheckViewModel();
        Object.assign(vm, {
          isAdmin,
          isHousehold: company.isHousehold,
          fein,
          legalName: legalName || '',
          matchedLegalName: legalNameVerified
        });
        return vm;
      })
    );
  }

  unverifyFein(fedTaxVm: FederalTaxViewModel) {
    this.companyService.unverifyFein(fedTaxVm);
  }

  updateFedTaxVm(
    fedTaxVm: FederalTaxViewModel,
    feinStatus: FeinStatus,
    fein: string,
    legalName: string,
    verifyAttempts: number,
    isServiceUnavailable: boolean) {

    legalName = legalName ? legalName.trim() : '';

    fedTaxVm.feinStatus = feinStatus;
    fedTaxVm.fein = fein;
    fedTaxVm.legalName = legalName;
    fedTaxVm.legalNameVerified = feinStatus === FeinStatus.Verified ? legalName : null;
    if (verifyAttempts) {
      fedTaxVm.feinValidationCount += verifyAttempts;
    }
    if (isServiceUnavailable) {
      // Set the reason for skip the verification control
      fedTaxVm.legalNameVerified = legalName;
      fedTaxVm.legalNameVerificationSkipReason = REASON_SERVICE_UNAVAILABLE;
    } else {
      fedTaxVm.legalNameVerificationSkipReason = null;
    }
  }

  get tinCheckCount() {
    return this.tinCheckService.tinCheckCount;
  }
}
