import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SharedModule } from '@shared/shared.module';

import { PageLeaveGuard } from '@app/core/routing-guards';
import { TinCheckModule } from '@app/tin-check/tin-check.module';

import { CompanyFedTaxInfoComponent } from './company-fed-tax-info/company-fed-tax-info.component';
import { CompanyStateTaxInfoComponent } from './company-state-tax-info/company-state-tax-info.component';
import { CompanyTaxInfoContainerComponent } from './company-tax-info-container/company-tax-info-container.component';
import { CompanyTaxComponent } from './company-tax/company-tax.component';


const routes: Routes = [
  {
    path: '',
    component: CompanyTaxInfoContainerComponent,
    children: [
      {
        path: '',
        component: CompanyFedTaxInfoComponent,
        canDeactivate: [PageLeaveGuard]
      },
      {
        path: 'state/:state',
        component: CompanyStateTaxInfoComponent,
        canDeactivate: [PageLeaveGuard]
      }
    ]
  }
];

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild(routes),
    TinCheckModule
  ],
  declarations: [
    CompanyFedTaxInfoComponent,
    CompanyStateTaxInfoComponent,
    CompanyTaxInfoContainerComponent,
    CompanyTaxComponent
  ]
})
export class CompanyTaxInfoModule {
  constructor() {
  }
}
