import { Injectable } from '@angular/core';

import { Observable } from 'rxjs';
import { first, map, concatMap } from 'rxjs/operators';

import { areDifferent } from '@core/utils';
import { HouseholdFilersService } from '@entity';
import { HouseholdFilers, HouseholdFilersStatus } from '@model';
import { Saver } from '@core';

@Injectable({providedIn: 'root'})
export class HouseholdFedTaxViewService {

  /** Observable of HouseholdFilers entity */
  hhFilers$: Observable<HouseholdFilers>;

  constructor(private householdFilersService: HouseholdFilersService) {
    this.hhFilers$ = this.householdFilersService.householdFilers$;
  }

  save(snapshot: HouseholdFilers): Observable<HouseholdFilers> {
    if (!snapshot) {
      return Saver.NO_SAVE; // no vm to save
    }
    return this.householdFilersService.householdFilers$.pipe(
      first(),
      concatMap(hhf => this.saveHouseholdFilers(snapshot, hhf))
    );
  }

  private saveHouseholdFilers(
    snapshot: HouseholdFilers,
    oldHhf: HouseholdFilers
  ): Observable<HouseholdFilers> {
    const newHhf: HouseholdFilers = { ...oldHhf, ...snapshot } as HouseholdFilers;
    // Check for difference with current HouseholdFiller
    if (areDifferent(newHhf, oldHhf, 'HouseholdFiller')) {
      return this.householdFilersService.upsert(newHhf);
    }
    return Saver.NO_SAVE;
  }
}
