import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { NgbTabset } from '@ng-bootstrap/ng-bootstrap';

import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

import { CompanyStateTaxViewService } from '../company-state-tax-view.service';
import { SubSink, WellKnownRoutes } from '@core';
import { tabRouteAnim } from '@animations';
import { TaxService } from '@app/core/services/tax-services/tax.service';
import { TaxBehaviorService } from '@app/core/services/tax-services/tax-behavior.service';

interface TabConfiguration {
  id: string; title: string; link: string; next: string;
}

@Component({
  selector: 'sp-co-tax-info-container',
  templateUrl: './company-tax-info-container.component.html',
  providers: [CompanyStateTaxViewService, TaxService, TaxBehaviorService],
  animations: [tabRouteAnim]
})
export class CompanyTaxInfoContainerComponent implements OnInit, OnDestroy {

  @ViewChild('tabset')
  tabset: NgbTabset;

  backLink: string = WellKnownRoutes.Employees;
  nextLink: string = WellKnownRoutes.BankAccount;
  nextLabel = 'Next Step';

  currentTabTitle: string;
  initialId: string; // initial tab id; tabsetSelect changes thereafter
  showTabs: boolean;
  private subs = new SubSink();
  tabConfigurations: TabConfiguration[];
  private tabLink: string; // link to the tab currently (or about to be) displayed

  constructor(
    private route: ActivatedRoute,
    private companyStateTaxViewService: CompanyStateTaxViewService,
    private ngTitle: Title
  ) {}

  ngOnInit(): void {
    this.companyStateTaxViewService.initialize();

    this.subs.sink = this.companyStateTaxViewService.taxStates$.subscribe(states => {
      this.showTabs = states.length > 0;
      if (this.showTabs) {
        this.nextLabel = 'Continue';

        const firstTab = [{ id: './', title: 'Federal', link: './', next: states[0] }];
        this.tabConfigurations = firstTab.concat(
          states.map((s, ix) => {
            const key = 'state/' + s;
            return { id: key, title: s, link: key, next: states[ix + 1] };
          })
        );
        this.currentTabTitle = this.tabConfigurations[0].title;
        this.watchRouteChange();
      }
    });
  }

  private getUrlPath(): string {
    if (this.route.snapshot.firstChild) {
      return this.route.snapshot.firstChild.url.join('/');
    }
    return '';
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  private setBrowserTabTitle() {
    this.ngTitle.setTitle(`SP Tax ${this.currentTabTitle}`);
  }

  /** Listen for route changes. Many of them re-use this component **/
  private watchRouteChange() {
    this.route.url.subscribe(url => {
      this.tabLink = this.getUrlPath() || this.tabConfigurations[0].link;
      this.initialId = this.initialId || this.tabLink; // only useful first time
      window.scrollTo(0, 0);
      // Wait a tick for tabs to draw
      setTimeout(() => this.updateCurrentTabInfo(this.tabLink));
    });
  }

  /**
   * When the current tab changes, setup the tab-related info
   * such as the tab title, the browser tab title, the "next" link,
   */
  private updateCurrentTabInfo(tabLink: string) {
    if (this.tabset) {
      this.tabset.select(tabLink); // constraint: select expects an ID and not a link -> forcing id == link
    }
    let tabIx = this.tabConfigurations.findIndex(tt => tt.link === tabLink);
    tabIx = tabIx < 0 ? 0 : tabIx;
    this.currentTabTitle = this.tabConfigurations[tabIx].title || '';
    const backTab = this.tabConfigurations[tabIx - 1];
    const nextTab = this.tabConfigurations[(tabIx += 1)];
    // On overflow, navigates away to the  BankAccount
    this.backLink = backTab ? backTab.link : WellKnownRoutes.Employees;
    this.nextLink = nextTab ? nextTab.link : WellKnownRoutes.BankAccount;
    this.nextLabel = nextTab ? 'Continue' : 'Next Step';
    this.setBrowserTabTitle();
  }
}
