import { Component, OnInit, ViewChild } from '@angular/core';
import { ngIfAnim } from '@animations';
import { ActivatedRoute } from '@angular/router';

import { ModelComponent } from '@app/shared';
import { CompanyStateTaxViewService } from '../company-state-tax-view.service';
import { ICompanyStateTaxes, TaxBehaviorWrapper } from '@app/core/services/tax-services/tax-models';
import { SaverFactoryService } from '@core';
import { WellKnownRoutes } from '@core/well-known-routes';
import { EmployeeTaxLocation } from '@entity';
import { CompanyTax } from '@app/model';

@Component({
  selector: 'sp-co-state-tax',
  templateUrl: './company-state-tax-info.component.html',
  styleUrls: ['../company-tax-info-tab.scss'],
  animations: [ngIfAnim]
})
export class CompanyStateTaxInfoComponent implements OnInit {
  vm: ICompanyStateTaxes;
  WellKnownRoutes = WellKnownRoutes;
  showOtherTaxes = false;

  state: string;
  stateName: string;

  @ViewChild(ModelComponent) modelContext: ModelComponent;

  constructor(
    private route: ActivatedRoute,
    private companyStateTaxViewService: CompanyStateTaxViewService,
    private saverFactory: SaverFactoryService
  ) { }

  ngOnInit() {
    // Set the current state to the routed url (e.g, 'CA')
    // Component may be re-used so subscribe for changes
    this.route.url.subscribe(url => {
      this.state = url[url.length - 1].path;
      this.stateName = this.companyStateTaxViewService.stateNames[this.state] || '';
      this.companyStateTaxViewService.setCurrentState(this.state);
    });

    // Get the VM powering the component
    this.companyStateTaxViewService.currentStateTaxVm$.subscribe(e => {
      this.vm = e;
    });
  }

  canLeaveAfterSave() {
    return this.save();
  }

  notVerified(stateEmployees: EmployeeTaxLocation[]): EmployeeTaxLocation[] {
    return stateEmployees.filter(e => e.isVerified === false);
  }

  hasVisibleTaxes(): boolean {
    return this.vm.taxes != null && this.vm.taxes.some(e => e.behavior.shouldShow);
  }

  getStateTaxes(requireInput: boolean): TaxBehaviorWrapper<CompanyTax>[] {
    return this.vm.taxes.filter(t =>
      t.behavior.shouldShow &&
      t.behavior.requireUserInput === requireInput
    );
  }

  hasOtherTaxes(): boolean {
    if (this.vm.taxes == null || this.vm.taxes.length === 0) {
      return false;
    }

    return this.vm.taxes.some(e => e.behavior.requireUserInput === false);
  }

  private save() {
    const saver = this.saverFactory.create()
      .add(this.companyStateTaxViewService.saveTaxes(this.vm));

    return saver.go();
  }
}
