
import { Injectable } from '@angular/core';

import { Observable } from 'rxjs';
import { first, map, concatMap, filter } from 'rxjs/operators';

import { CompanyService } from '@entity';

import { areDifferent } from '@core/utils';
import { Company, FeinStatus } from '@model';
import { FederalTaxViewModel } from './company-tax-info-vm';
import { Saver } from '@core';
import { setFeinStatus } from './tax-utils';
import { ICompanyFederalTaxes } from '@app/core/services/tax-services/tax-models';
import { TaxCollectorService } from '@app/core/services/tax-services/tax-collector.service';


@Injectable({ providedIn: 'root' })
export class CompanyFedTaxViewService {

  /** The current company's federal tax view model*/
  fedTaxVm$: Observable<FederalTaxViewModel>;

  /** The federal company taxes */
  federalTaxes$: Observable<ICompanyFederalTaxes>;

  constructor(private companyService: CompanyService,
    private taxCollectorService: TaxCollectorService) {

    this.fedTaxVm$ = this.companyService.currentCompany$.pipe(
      map(company => this.toVm(company))
    );

    this.federalTaxes$ = this.taxCollectorService.taxes$.pipe(
      filter(taxes => taxes.companyFederalTaxes != null),
      map(taxes => taxes.companyFederalTaxes)
    );
  }

  saveCompany(vm: FederalTaxViewModel): Observable<FederalTaxViewModel> {
    if (!vm) {
      return Saver.NO_SAVE; // no vm to save
    }

    return this.companyService.currentCompany$.pipe(
      first(),
      concatMap(company => this.saveCompanyVm(vm, company))
    );
  }

  saveTaxes(taxes: ICompanyFederalTaxes): Observable<any> {
    if (!taxes) {
      return Saver.NO_SAVE; // no vm to save
    }

    return this.taxCollectorService.saveCompanyFederalTaxes(taxes);
  }

  private saveCompanyVm(vm: FederalTaxViewModel, company: Company): Observable<FederalTaxViewModel> {
    let fedVm = vm;
    const { fein, feinStatus } = setFeinStatus(
      company.fein,
      company.feinStatus,
      fedVm.fein,
    );

    // fix to save FeinStatus when Verified
    // TODO: (Review wirth Ward and move inside setFeinStatus ??)
    let feinStatus2: FeinStatus;
    if (vm.feinStatus === FeinStatus.Verified && feinStatus !== FeinStatus.Verified) {
      feinStatus2 = FeinStatus.Verified;
    } else {
      feinStatus2 = feinStatus;
    }
    // end fix

    fedVm = {
      ...fedVm,
      legalName: fedVm.legalName.trim(),
      legalNameVerified: fedVm.legalNameVerified ? fedVm.legalNameVerified.trim() : null,
      fein,
      feinStatus: feinStatus2,
      feinValidationCount: fedVm.feinValidationCount
    };

    const newCo = { ...company, ...fedVm };
    // Check for difference with current company
    if (areDifferent(newCo, company, 'Company Tax')) {
      return this.companyService.update(newCo).pipe(
        map(co => this.toVm(co))
      );
    }
    return Saver.NO_SAVE;
  }

  private toVm(company: Company): FederalTaxViewModel {
    const vm: FederalTaxViewModel = {
      isHousehold: company.isHousehold,
      isAgricultural: company.isAgricultural,
      legalName: company.legalName || '',
      legalNameVerified: company.legalNameVerified || null,
      fein: company.fein || '',
      feinStatus: company.feinStatus,
      feinValidationCount: company.feinValidationCount,
      legalNameVerificationSkipReason: company.legalNameVerificationSkipReason
    };
    return vm;
  }
}
