import { Component, OnInit, ViewChild, ElementRef,
         Input, EventEmitter, Output } from '@angular/core';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';

import { Observable } from 'rxjs';

import { fadeAnimation } from '@animations';
import { FederalTaxViewModel } from '@app/company-tax-info/company-tax-info-vm';
import { FeinStatus } from '@model';
import { isBadFeinNumber, TinCheckResponse } from '@core/services/tin-check.service';
import { TinCheckViewModel } from './tin-check-vm';
import { TinCheckViewService, MAX_VALIDATION_COUNT } from './tin-check-view.service';

export interface FeinValidationPayload {
  fein: string;
  legalName: string;
  feinStatus: FeinStatus;
}

export enum TinCheckUIState {
  Verifying = 'Verifying',
  Verified = 'Verified',
  Mismatch = 'Mismatch',
  NoMatch = 'NoMatch',
  InvalidFein = 'InvalidFein',
  EditData = 'EditData',
  Close = 'Close',
  Blocked = 'Blocked',
  MissingLegalName = 'MissingLegalName',
  ServiceUnavailable = 'ServiceUnavailable'
}

@Component({
  selector: 'sp-tin-check',
  templateUrl: './tin-check.component.html',
  animations: [ fadeAnimation ]
})
export class TinCheckComponent implements OnInit {
  @Input() fedTaxVm: FederalTaxViewModel;

  @Output() feinChange = new EventEmitter<FeinValidationPayload>();

  @ViewChild('tinModal') tinModal: ElementRef;

  FeinStatus = FeinStatus;
  TinCheckUIState = TinCheckUIState;

  isBadFeinNumber = isBadFeinNumber;
  isVerifying = false;
  private modalRef: NgbModalRef;
  readOnly$: Observable<boolean>;
  private startingTinCheckCount: number;
  uiStatus: TinCheckUIState;
  vm: TinCheckViewModel;

  constructor(
    private modalService: NgbModal,
    private tinCheckViewService: TinCheckViewService,
  ) {
    this.readOnly$ = tinCheckViewService.readOnly$;
  }

  ngOnInit() {
    this.startingTinCheckCount = this.tinCheckViewService.tinCheckCount;
    this.tinCheckViewService.getTinCheckViewModel(this.fedTaxVm).subscribe(
      vm => this.vm = vm,
      error => {
        this.vm = new TinCheckViewModel();
        this.uiStatus = TinCheckUIState.ServiceUnavailable;
      });
  }

  // #region Admin-only
  /** Reset Attempts. Admin only functionality */
  adminResetAttempts() {
    this.fedTaxVm.feinValidationCount = 0;
    this.closeModal();
  }

  /** Force as verified: Admin only functionality */
  adminSetVerified() {
    this.uiStatus = TinCheckUIState.Verified;
    this.tinCheckViewService.updateFedTaxVm(this.fedTaxVm,
      FeinStatus.Verified, this.vm.fein, this.vm.legalName, 0, true);
    this.closeModal();
  }

  /** Force as unverified: Admin only functionality */
  adminSetUnverified() {
    this.tinCheckViewService.updateFedTaxVm(this.fedTaxVm,
      FeinStatus.Unverified, this.vm.fein, this.vm.legalName, 0, true);
    this.closeModal();
  }
  // #endregion Admin-only

  close(reason: string) {
    if (reason === 'use-match') {
      this.vm.legalName = this.vm.matchedLegalName;
      this.tinCheckViewService.updateFedTaxVm(this.fedTaxVm,
        FeinStatus.Verified, this.vm.fein, this.vm.legalName, 0, !this.serviceIsAvailable);
    }
  }

  closeModal() {
    if (this.modalRef && this.modalRef.close) {
      this.modalRef.close('ok');
      this.modalRef = null;
    }
  }

  /** disable re-verify unless user changed something that warrants re-checking  */
  disableModalVerify(vm: TinCheckViewModel) {
    const legalName = vm.legalName ? vm.legalName.trim().toUpperCase() : '';
    /* Don't verify if
     * - Currently verifying
     * - No legal name
     * - Bad FEIN
     * - No change to FEIN or legal name
     */
    return this.isVerifying ||
      !legalName ||
      isBadFeinNumber(vm.fein) ||
      (vm.fein === vm.preEditFein && vm.legalName === vm.preEditLegalName);
  }

  editData() {
    this.uiStatus = TinCheckUIState.EditData;
  }

  get haveData() {
    return this.fedTaxVm && this.vm;
  }

  get serviceIsAvailable(): boolean {
    return this.uiStatus !== TinCheckUIState.Blocked && this.uiStatus !== TinCheckUIState.ServiceUnavailable;
  }

  unverifyFein() {
    this.tinCheckViewService.unverifyFein(this.fedTaxVm);
  }

  validate() {
    this.vm.fein = this.fedTaxVm.fein;
    if (isBadFeinNumber(this.fedTaxVm.fein)) {
      return; // do not open or check
    }

    if (this.fedTaxVm.feinStatus === FeinStatus.Verified) {
      return; // already in a verified state
    }

    // Open modal if blocked, unverified, or invalid
    if (this.checkIfBlocked() ||
        this.fedTaxVm.feinStatus === FeinStatus.Unverified ||
        this.fedTaxVm.feinStatus === FeinStatus.Invalid) {
      this.openModalDialog();
    }

    this.runTinCheck();
  }

  private openModalDialog() {
    this.vm.preEditFein = this.vm.fein,
    this.vm.preEditLegalName = this.vm.legalName,
    this.modalRef = this.modalService.open(this.tinModal);
    this.modalRef.result.then(res => {
      this.close(res);
      this.modalRef = null;
    });
  }

  // #region runTinCheck
  /* verify FEIN/LegalName with TinCheck service **/
  runTinCheck() {
    if (this.checkIfBlocked()) {
      this.isVerifying = false;
      return;
    }
    this.isVerifying = true;
    this.tinCheckViewService.check(this.vm.fein, this.vm.legalName)
      .subscribe(
        r => this.onTinChecked(r),
        error => this.onTinCheckError(error)
      );
  }

  /**
   * Return true if TinCheck blocked because too many attempts.
   * If true, also sets UI status to "Blocked"
   */
  private checkIfBlocked(): boolean {
    if (this.fedTaxVm.feinValidationCount <= MAX_VALIDATION_COUNT) {
      return false;
    } else {
      this.uiStatus = TinCheckUIState.Blocked;
      return true;
    }
  }

  private onTinChecked(r: TinCheckResponse): void {
    this.isVerifying = false;
    const attempts = Math.max(0, this.tinCheckViewService.tinCheckCount - this.startingTinCheckCount);
    this.startingTinCheckCount = this.tinCheckViewService.tinCheckCount; // Reset baseline for next time.
    this.vm.serviceResponse = this.toServiceResponse(r.serviceResponse);

    if (r.verified) {
      this.uiStatus = TinCheckUIState.Verified;
      this.tinCheckViewService.updateFedTaxVm(this.fedTaxVm,
        FeinStatus.Verified, r.fein, r.matchedName, attempts, false);
      this.closeModal();

    } else if (r.invalid)  {
      this.uiStatus = TinCheckUIState.InvalidFein;
      this.tinCheckViewService.updateFedTaxVm(this.fedTaxVm,
        FeinStatus.Invalid, this.vm.fein, this.vm.legalName, attempts, false);
      this.checkIfBlocked();

    } else if (r.error)  {
      this.uiStatus = TinCheckUIState.ServiceUnavailable;

    } else if (r.serviceUnavailable)  {
      this.uiStatus = TinCheckUIState.ServiceUnavailable;
      this.tinCheckViewService.updateFedTaxVm(this.fedTaxVm,
        FeinStatus.Unverified, this.vm.fein, this.vm.legalName, 0, true);
      this.closeModal();

    } else {
      this.vm.matchedLegalName = r.matchedName;
      this.uiStatus = r.matchedName ? TinCheckUIState.Mismatch : TinCheckUIState.NoMatch;
      this.tinCheckViewService.updateFedTaxVm(this.fedTaxVm,
        FeinStatus.Invalid, this.vm.fein, this.vm.legalName, attempts, false);
      this.checkIfBlocked();
    }
  }

  private onTinCheckError(error: any) {
    this.isVerifying = false;
    this.uiStatus = this.fedTaxVm.legalName
      ? TinCheckUIState.ServiceUnavailable : TinCheckUIState.MissingLegalName;
  }

  private toServiceResponse(r: any) {
    try {
      return 'TinCheck service response: ' + JSON.stringify(r, null, 2);
    } catch (e) {
      return 'TinCheck service response error';
    }
  }
  // #endregion runTinCheck
}
