import { FeinStatus, SeinStatus, SuiStatus } from '@model';

/** Calculate Fed tax id (fein) status from old and new values */
export function setFeinStatus(
  oldFein: string,
  oldFeinStatus: FeinStatus,
  newFein: string,
) {
  const {taxId, taxIdStatus, appliedFor } = setTaxIdStatus(oldFein, oldFeinStatus, newFein);
  return {
    fein: taxId,
    feinStatus: taxIdStatus,
  };
}

/** Calculate State tax id (sein) status from old and new values */
export function setSeinStatus(
  oldSein: string,
  oldSeinStatus: SeinStatus,
  newSein: string,
  newAppliedFor: boolean
) {
  const {taxId, taxIdStatus, appliedFor } = setTaxIdStatus(oldSein, oldSeinStatus, newSein, newAppliedFor);
  return {
    sein: taxId,
    seinStatus: taxIdStatus,
    seinAppliedFor: appliedFor
  };
}

/** Calculate State Unemployment (sui) id status from old and new values */
export function setSuiStatus(
  oldSuid: string,
  oldSuiStatus: SuiStatus,
  newSuid: string,
  newAppliedFor: boolean
) {
  const {taxId, taxIdStatus, appliedFor } = setTaxIdStatus(oldSuid, oldSuiStatus, newSuid, newAppliedFor);
  return {
    suid: taxId,
    suiStatus: taxIdStatus,
    suidAppliedFor: appliedFor
  };
}

// Logic in common for all three
function setTaxIdStatus<T extends string>(
  oldTaxId: string,
  oldTaxIdStatus: T,
  newTaxId: string,
  newAppliedFor?: boolean
) {
  newTaxId = (newTaxId || '').trim();

  let result: { taxId: string, taxIdStatus: T, appliedFor: boolean };

  if (newTaxId === oldTaxId) {
    result = {
      taxId: oldTaxId,
      taxIdStatus: oldTaxIdStatus,
      appliedFor: newTaxId ? false : newAppliedFor === true
    };
  }
  if (newTaxId) {
    result = {
      taxId: newTaxId,
      taxIdStatus: SeinStatus.Unverified as T,
      appliedFor: false
    };
  } else  {
    result = {
      taxId: null,
      taxIdStatus: (newAppliedFor ? SeinStatus.AppliedFor : null) as T,
      appliedFor: newAppliedFor === true
    };
  }
  return result;
}
