import { TaxCollectorService } from '@app/core/services/tax-services/tax-collector.service';
import { Injectable, OnDestroy } from '@angular/core';

import { Observable, Subject, BehaviorSubject, combineLatest } from 'rxjs';
import { distinctUntilChanged, filter, first, concatMap, map } from 'rxjs/operators';

import { Indexable } from '@core';

import { CompanyService } from '@entity/entity-services';

import { Company } from '@model';
import { TaxLocationService } from '@entity';
import { USStateCodes } from '@model/codes-enums';
import { ICompanyStateTaxes } from '@app/core/services/tax-services/tax-models';
import { Saver } from '@core/saver';

@Injectable({ providedIn: 'root' })
export class CompanyStateTaxViewService implements OnDestroy {

  /** The state name of the current state tax in this service */
  private currentStateSubject = new BehaviorSubject<string>('');
  currentState$ = this.currentStateSubject.asObservable();

  /** The ViewModel for the current state tax in this service. Undefined if no current state. */
  currentStateTaxVm$: Observable<ICompanyStateTaxes>;

  /** States of the current company's in which taxes will be owed  */
  taxStates$: Observable<string[]>;

  /** Map of state codes to full state names */
  stateNames: Indexable;

  /** Company  */
  currentCompany$: Observable<Company>;

  private initialized = false;
  private destroy$ = new Subject();

  constructor(
    private companyService: CompanyService,
    private taxCollectorService: TaxCollectorService,
    private taxLocationService: TaxLocationService,
  ) {
    this.stateNames = USStateCodes.codes.reduce(
      (acc, code) => {
        acc[code.value] = code.displayName;
        return acc;
      },
      {} as Indexable
    );

    this.currentState$ = this.currentStateSubject.pipe(distinctUntilChanged());
    this.taxStates$ = this.taxLocationService.companyTaxStates$;
    this.currentCompany$ = this.companyService.currentCompany$;
    this.currentStateTaxVm$ = combineLatest(this.currentState$, this.taxCollectorService.taxes$)
      .pipe(
        filter(([state, taxes]) => state != null && state !== '' && taxes.companyStateTaxes != null),
        map(([state, taxes]) => {
          const stateTaxes = taxes.companyStateTaxes.find(t => t.state === state);
          return stateTaxes;
        }),
        first());
  }

  // Save the current State Tax VM (if any), then unsubscribe
  ngOnDestroy() {
    // unsubscribe so we block further updates, if we update the store
    // then we wil re-trigger vm changes
    this.destroy$.next();
    // save changes
    this.currentStateSubject.next('');
  }

  /**
   * Set the current state, saving the prior state tax VM (if any), and
   * triggering a new state tax VM in currentStateTaxVm$ */
  setCurrentState(state?: string) {
    this.currentStateSubject.next(state || '');
  }

  /**
   * Set up all State Tax observables and subscribe to state VM changes that save
   * Must call before consuming other service methods */
  initialize(): void {
    // Don't initialize twice. Paranoia
    if (this.initialized) {
      return;
    }
    this.initialized = true;
  }

  /** Save the State Tax, given the state, state company taxes and stored company taxes record */
  public saveTaxes(companyStateTaxes: ICompanyStateTaxes): Observable<any> {
    if (!companyStateTaxes) {
      return Saver.NO_SAVE; // no vm to save
    }

    return this.taxCollectorService.saveCompanyStateTaxes(companyStateTaxes);
  }
}
